import { lazy, Suspense, useEffect, useRef, useState } from 'react';
import './index.css'

import { Link, Redirect, Route, Switch, useLocation } from 'react-router-dom';
import menu from 'views/Main/menu';
import Loading from 'component/Loading';

const routers: any = menu.reduce((result, cur: any) => {
    if (cur.sub) return result.concat(cur.sub)
}, []) || [];


const Main = (props: any) => {

    const location = useLocation();
    const ref = useRef() as any;
    const { pathname } = location;



    useEffect(() => {
        if (ref.current) {
            ref.current.scrollTop = 0;
        }
    }, [pathname])

    return (
        <div className="main-container">
            {/* <div className="menu">侧边栏侧边栏啦啦啦除了宝贝</div> */}
            <div className="menu">
                {
                    menu.map((item) => (
                        <div key={item.key} className="item-container">
                            <div className="menu-header">{item.name}</div>
                            {
                                item.sub.map((sub) => (
                                    <Link
                                        key={sub.key}
                                        to={`/${sub.key}`}
                                        className={pathname === `/${sub.key}` ? "sub-item sub-active" : 'sub-item'}
                                    // onClick={() => {
                                    //     // history.push(`/${sub.key}`)
                                    // }}
                                    >{sub.name}</Link>
                                ))
                            }
                        </div>
                    ))
                }
            </div>

            <div className="content" ref={ref}>
                <div className="content-width">
                    <Switch>
                        {
                            routers.map((item: any) => (
                                <Route
                                    key={item.key}
                                    path={`/${item.key}`}
                                    render={() => (
                                        <Suspense fallback={<div className="center">
                                            <Loading className="loading" />
                                        </div>}>
                                            <item.component />
                                        </Suspense>
                                    )}
                                />
                            ))
                        }

                        <Redirect path="/" to="/calender"></Redirect>
                    </Switch>
                </div>
            </div>


        </div>
    )
}

export default Main;